
<template>
<div class="mycarport">
    <el-table :data="listData" size="small" border  style="margin-top:20px;width:100%;"  v-loading="loading"
    :default-sort = "{prop: 'date', order: 'descending'}" @sort-change='dataSort(column, prop, order)'>
        <el-table-column align="center" prop="OrderCode" label="" sortable='custom' ></el-table-column>
        <el-table-column align="center" prop="ParkingCode" label="" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="BuildingName" label="" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="Price" label="" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="FixedRate" label="" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="EnsureMark" label="" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="RepoTerm" label="" ortable='custom'></el-table-column>
        <el-table-column align="center" label="" width="450" fixed="right">
            <template slot-scope="scope">
                <!-- <el-button type="primary" size="mini" @click="detailsopen(scope.row)"></el-button> -->
                <el-button type="info" size="mini" @click="carportEdit(scope.row.ParkingId)"></el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)"></el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)"></el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)"></el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)"></el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-dialog
    title=""
    :visible.sync="cardDeitDialog"
    width="50%">
    <el-form ref="form" :model="form" label-width="80px">
        <el-row>
            <el-col :span="12">
                <el-form-item label=":">
                    <span>{{form.Acreage == null?'':form.Acreage}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.Contact == null?'':form.Contact}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.ContactTel == null?'':form.ContactTel}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.Instruction == ''?'':form.Instruction}}</span>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label=":">
                    <span>{{form.Location == null?'':form.Location}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.OwnnerId == null?'':form.OwnnerId}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.ParkingCode == null?'':form.ParkingCode}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.ParkingType == null?'':form.ParkingType}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.PledgeMark == null?'':form.PledgeMark}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.Price == null?'':form.Price}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.ProtocolName == null?'':form.ProtocolName}}</span>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.State == 0?'':form.State == 1?'':form.State == 2?'':form.State == -1?'':form.State == 3?'':form.State == 4?'':form.State == 5?'':form.State == 6?'':''}}</span>
                </el-form-item>
               
                <el-form-item label=":">
                    <el-button type="primary" size='mini'>{{form.EnsureMark == 1?'':form.EnsureMark == -1?'':''}}</el-button>
                </el-form-item>
                <el-form-item label=":">
                    <span>{{form.Type == null?'':form.Type}}</span>
                </el-form-item>
            </el-col>
        </el-row>
        <el-form-item label=":">
            <span>{{form.Description}}</span>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="orderDeitDialog = false"> </el-button>
        <el-button type="primary" @click="orderDeitDialog = false"> </el-button>
    </span>
    </el-dialog>
    <div class="pagination-container">
        <el-pagination background @size-change="setSize" @current-change="setPage" :current-page="listQuery.page" :page-sizes="[10,20,30,50]" :page-size="listQuery.size" layout="total, sizes, prev, pager, next, jumper" :total="listQuery.total">
        </el-pagination>
    </div>
</div>

</template>


<script>
import { pageSet } from '@/utils/mixins.js'
  export default {
    name:'MyCarport',
    mixins: [pageSet],
    data() {
        return {
            listQuery:{
                // rows:10,
                page: 1,
                rows: 10,
                total: 1,
            },
            form: {
                Acreage: '',
                Contact: '',
                ContactTel: '',
                Instruction: '',
                Location: '',
                OwnnerId: '',
                ParkingCode: '',
                ParkingType: '',
                PledgeMark: '',
                Price: '',
                ProtocolName: '',
                State: '',
                EnsureMark: '',
            },
            cardDeitDialog:false,
            listData: [],
            loading: true,
        };
    },
    methods: {
        getData(){
            let objQuery = {}
            objQuery.ParkingCode=''
            objQuery.state='0,1,2,3,4,5,6'
            this.$api.userInfoApi.ParkingInfo(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    queryJson:JSON.stringify(objQuery),
                    data:JSON.stringify(this.listQuery)
                }
            ).then(res=>{
                if(res.data.code=='200'){
                    this.listData = res.data.data.rows
                    this.listQuery.total = res.data.data.records
                    this.loading = false
                }
                if(res.data.code=='400'){
                    this.loading = true
                    this.$message.error('')
                }
            }).catch(error=>{
                this.$message.error('')
                this.loading = false
            })
        },
        dataSort(column, prop, order){
            console.log(column, prop, order)
        },
        setPage(val) {
            this.listQuery.page = val
            this.getData()
        },
        setSize(val) {
            this.listQuery.size = val
            this.getData()
        },
        detailsopen(){

        },
        add(){

        },
        formatter(row, column) {
            return row.address;
        },
        carportEdit(id){
            // console.log(row.row.id)
            //  this.$router.push({
            //     path: '/home',
            //     query: {
            //         id: row.row.id
            //     }
            // })
            this.cardDeitDialog = true
            console.log(id)
            this.loading=true
            this.$api.userInfoApi.ParkingDetail(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    parkingId:id
                }
            ).then(res=>{
                console.log(res)
                this.loading = false
                // this.form = res.data.data.data[0]
                
            }).catch(error=>{
            })
        }
    },
    created() {
        this.getData()
    }
  }
</script>
<style scoped>
.mycarport .pagination-container{
    margin-top:20px;
    text-align: center;
}
</style>